.panel {
  position: absolute;
  border-radius: 3px;
  border: 3px solid #030921;
  background-color: #f1f2fa;
  color: rgb(27, 27, 27);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 75%;
  width: 75%;
  box-shadow: 8px 10px 3px rgba(0, 0, 0, 0.7);
}
.panel .top_bar {
  height: 30px;
  width: 100%;
  border-radius: 3px 3px 0 0;
  background-color: #f1f2fa;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: move;
  border-bottom: 3px solid #030921;
}
.top_bar .descriptor {
  margin-left: 0.75rem;
}
.top_bar .buttons {
  display: flex;
  margin-right: 0.75rem;
}
.top_bar .bar_click {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-left: 5px;
  cursor: pointer;
  border: 1px solid black;
}
.panel .content {
  position: relative;
  height: calc(100% - 35px);
  overflow-y: auto;
}
.top_bar #minimize {
  background-color: rgb(159, 255, 196);
}
.top_bar #maximize {
  background-color: rgb(236, 234, 193);
}
.top_bar #close {
  background-color: rgb(255, 121, 121);
}